.sliding-pane-component {
    background-color: rgba(0, 0, 0, 0.2);
    position: fixed;
    width: 100vw;
    left: 0;
    right: 0;
    top: 100vh;
    bottom: 0;
    /* height: 100vh; */
    overflow: hidden;
    display: flex;
    align-items: stretch;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.sliding-pane-inner-container {
    flex-grow: 1;
    display: flex;
    align-items: stretch;
}

.sliding-pane-inner-container>* {
    flex-grow: 1;
}

.sliding-pane-component--showing.sliding-up {
    top: 25vh;
    border-radius: 20px 20px 0 0;
}