.controls-component {
    position: fixed;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.1);
    width: 100vw;
    height: 2.7rem;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
}

.control-component {
    width: 2.7rem;
    background-color: rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
}

.control-component:hover {
    background-color: rgba(0, 0, 0, 0.2);
}

.bg-parenthesis {
    background-image: url('/imgs/parenthesis-black.png');
    background-size: cover;
    /* background-attachment: fixed; */
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
}

.showing-parenthesis .bg-parenthesis {
    background-color: rgb(25, 94, 173) !important;
    background-image: url('/imgs/parenthesis-white.png');
}