.conversion-item {
    margin-top: 1rem;
    border: 1px solid #333;
    border-radius: 4px;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
    margin: .5rem auto;
    padding-right: 1rem;
    padding-left: .5rem;
}

.conversion-item .conversion-value {
    position: relative;
    padding-bottom: .3rem;
    border-radius: 4px;
    padding-top: 2rem;
    min-width: 172px;
}

.si-object-value-container {
    display: flex;
    flex-direction: column;
    position: relative;
    /* opacity: .2; */
}

.si-object-value-container:hover {
    opacity: 1;
}

.si-object-value-container::before,
.si-object-value-container::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    height: 1px;
    background-color: #aaa;
}

.si-object-value-container::after {
    transform: scale(0);
    height: 2px;
    background-color: black;
}

.si-object-value-container:hover::after {
    transform: scale(1);
}

.si-object-value-container .si-object-value {
    padding: .1rem;
    border-radius: 4px;
    text-align: center;
    border: none;
    margin-top: .5rem;
    background-color: transparent;
    font-size: 1.2rem;
    padding: .5rem;
}

.si-object-value-container .si-object-value::placeholder {
    color: #aaa;
}

.si-object-value-container .si-object-value:focus {
    color: black;
    outline: none;
}

.si-object-value-container .edit-icon {
    right: 0rem;
    top: .3rem;
    position: absolute;
    font-size: 1.2rem;
    top: .7rem;
}

.si-object-value-container:hover .edit-icon {
    color: black;
}

.conversion-item .conversion-units {
    cursor: pointer;
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.conversion-item .conversion-units .edit-icon {
    position: absolute;
    right: 0;
    bottom: .4rem;
    font-size: .9rem;
    opacity: .2;
}

.conversion-item--not-editable .conversion-value {
    padding: 2rem 0;
}

.si-object-units-container {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 3.5rem 1fr 1fr 5rem;
    gap: 0;
    height: 100%;
}

.si-object-units-container .container {
    margin: 0 .2rem;
    margin-bottom: 2.5rem;
    background-color: #333;
    overflow-y: scroll;
}


/*Suffix compoenent*/

.suffix-component {
    min-height: 3rem;
    background-color: #eee;
    display: grid;
    padding: .2rem;
    border-bottom: 1px solid gray;
    /* border-radius: 4px; */
    grid-template-columns: 1.8rem 1fr;
    grid-template-rows: 1fr 1fr;
    font-weight: bold;
}

.suffix-component .suffix-name {
    grid-column: 1/3;
    padding-bottom: .2rem;
    color: #777;
    font-size: .7rem;
}

.suffix-component .suffix-exponent {
    position: relative;
    color: #999;
    font-weight: normal;
}

.suffix-component .suffix-exponent span {
    font-size: .7rem;
    position: absolute;
    color: #333;
    font-weight: bold;
}

.suffix-component.suffix-component--active {
    color: white;
    background-color: rgb(67, 135, 218);
}

.suffix-component.suffix-component--active .suffix-name {
    color: #bababa;
}

.suffix-component.suffix-component--active .suffix-exponent {
    color: #999;
}

.suffix-component.suffix-component--active .suffix-exponent span {
    color: white;
}


/*Unit DEfinitions compoenent*/

.definition-component {
    min-height: 3rem;
    background-color: #eee;
    display: grid;
    padding: .2rem;
    border-bottom: 1px solid gray;
    /* border-radius: 4px; */
    grid-template-columns: 1fr 1.2rem;
    grid-template-rows: 1rem 1.5rem .8rem;
    font-weight: bold;
}

.definition-component .definition-name {
    grid-column: 1/3;
    padding-bottom: .2rem;
    color: #777;
    font-size: .7rem;
}


/* .definition-component .definition-symbol {} */

.definition-component .definition-details {
    color: #999;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1rem;
    height: 1rem;
    background-color: transparent;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.definition-component .definition-measurement {
    font-size: .5rem;
    grid-column: 1/3;
    color: #555;
    text-align: right;
}

.definition-component.definition-component--active {
    color: white;
    background-color: rgb(67, 135, 218);
}

.definition-component.definition-component--active .definition-name {
    color: #bababa;
}

.definition-component.definition-component--active .definition-exponent {
    color: #999;
}

.suffix-component.suffix-component--active .suffix-exponent span {
    color: white;
}


/*Unit Compoenent*/

.unit-component {
    height: 3rem;
    background-color: #eee;
    border-bottom: 1;
    padding: .2rem;
    border-bottom: 1px solid gray;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
}

.unit-component.unit-component--add {
    background-color: #aaa;
    color: white;
    font-size: 1.5rem;
    margin-top: .3rem;
}

.unit-component .unit-component--remove {
    background-color: #aaa;
    color: white;
    font-size: .8rem;
    position: absolute;
    top: 0;
    width: 1rem;
    right: 0;
    height: 1rem;
    right: -100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.unit-component:hover .unit-component--remove {
    right: 0;
}

.unit-component.unit-component--active {
    color: white;
    background-color: rgb(67, 135, 218);
}